<template>
  <div class="login_page">
    <el-card class="el-card">
      <div slot="header">
        <h2 class="login-title">咨询师登录</h2>
      </div>
      <el-form ref="loginForm" :model="form" :rules="rules" label-width="80px" class="login-box">
        <el-form-item class="form" label="账号" prop="username">
          <el-input type="text" placeholder="请输入账号" v-model="form.username"/>
        </el-form-item>
        <el-form-item class="form" label="密码" prop="password">
          <el-input type="password" placeholder="请输入密码" @keyup.enter="enterSearchMember" v-model="form.password" show-password/>
        </el-form-item>
        <el-form-item id="forget-remember">
          <el-checkbox style="margin-left: 40px" v-model="checked">记住密码</el-checkbox>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" style="margin-left: 55px;" @click="onSubmit('loginForm')">登录
          </el-button>
          <el-button type="plain" class="btn2" style="margin-left: 70px" @click="get()">返回</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Login",
  data() {
    return {
      form: {
        username: '',
        password: ''
      },

      user: "",
      hukouben_name:'',
        checked:true,
      //表单验证，需要在 el-form-item 元素中增加prop属性
      rules: {
        username: [
          {required: true, message: "账号不可为空", trigger: "blur"}
        ],
        password: [
          {required: true, message: "密码不可为空", trigger: "blur"}
        ]
      },
    }
  },

  methods: {
    onSubmit() {
      if(this.form.username == ""){this.$message.warning("请输入用户名！")}
      else if(this.form.password == ''){this.$message.warning("请输入密码！")}
      else {
        axios({
          method:'POST',
          url:'api/teachers/signin',
          data:{
            account:this.form.username,
            password:this.form.password
          }
        }).then((resp)=>{
          console.log(resp.data)
          this.user = this.form.username;
          if (resp.data.ret === 0) {
            window.sessionStorage.setItem("user", this.form.username);
            this.$router.push('consult_student');
            this.$message.success("登录成功");
            this.$notify.closeAll();
            this.$cookies.set('teacher_name', this.form.username,'0', {path: '/consult_student'});
            if(this.checked == true){
              this.setCookies(this.form.username,this.form.password,7);
            }else {
              this.clearCookies();
            }
          }
          else{
            this.$message.error(resp.data.msg)
          }
        })
      }
    },
    open2() {
      let data = [
        '为您提供账号和密码',
        '账号：admin',
        '密码：123123',
        '(此消息30s后消失)'];
      let newDatas = [];
      const h = this.$createElement;
      for (let i in data) {
        newDatas.push(h('p', null, data[i]));
      }
      this.$notify({
        title: '尊敬的各位评委老师',
        message: h('div', null, newDatas),
        duration: 30000,
      });
    },
    get() {
      this.$router.push('/')
      this.$notify.closeAll();
    },

    enterSearchMember() {
      this.onSubmit()  //回车之后的执行的方法
    },
      // 设置Cookies
      setCookies(tea_name, tea_pwd, tea_time) {
          var exdata = new Date();
          // 设置保存时间
          exdata.setTime(exdata.getDay() + 24 * 60 * 60 * 1000 * tea_time);

          // 拼接Cookies
          var expare = exdata.toUTCString();
          window.document.cookie = 'teaName' + '=' + tea_name + ";" + expare + ";path=/";
          window.document.cookie = 'teaPwd' + '=' + tea_pwd + ";" + expare + ";path=/";
      },

      // 获取Cookies
      getCookies(teaname, teapwd) {
          var name = teaname + "=";
          var pwd = teapwd + "=";
          // 对Cookies进行解码
          var decodedCookies = decodeURIComponent(document.cookie);

          // 将Cooies进行拆分，放到ca数组中
          var ca = decodedCookies.split(';');

          for (let i = 0; i < ca.length; i++) {
              // 读取每一个ca的值
              var c = ca[i].split('=');
              if (c[0] == ' teaName') {
                  this.form.username = c[1];
              } else if (c[0] == ' teaPwd') {
                  this.form.password = c[1];
              }
          }
          return "";
      },

      // 清除Cookies
      clearCookies: function () {
          this.setCookies("", "", -1);
      },
  },
    created() {
    this.open2()
      this.getCookies(this.form.username,this.form.password);
      const _this = this;
      document.onkeydown = function (e) {
        var key = window.event.keyCode;
        if (key == 13) {
          _this.enterSearchMember();
        }
      }
    }

}
</script>

<style scoped>
.login_page {
  margin: auto;
  /*width: 100%;*/
  /*height: 100%;*/
  background: url('../assets/admain1.png');
  /*filter: blur(5px);*/
  /*background-size: 100% 100%;*/
  background-size: cover;
  position: fixed;
  background-attachment: fixed !important;
  /*position absolute*/
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.el-card {
  margin: auto;
  width: 520px;
  height: 380px;
  /*margin-left: 720px;*/
  margin-top: 100px;
  border-radius: 40px;
}

.login-title {
  text-align: center;
  margin: 20px auto 8px auto;
  color: #303133;
}

.form {
  width: 340px;
  margin-left: 50px;
  margin-top: 5px;
}

.btn2 {
  background-color: #70f6e1;
  color: white;
}
</style>
